/* PROGRESS BAR WRAPPER */
.onboarding-progress-bar {
  width: 220px;
  height: 6px;
  background-color: #e0e0e0;
  border-radius: 10px;
  margin: 24px 0 28px;
  position: relative;
  overflow: hidden;
  z-index: 5;
}

/* ACTIVE FILL (STEP 1 OF 3) */
.onboarding-progress-fill {
  width: 33.33%;
  height: 100%;
  background-color: #042954;
  border-radius: 10px;
}

.onboarding-progress-fill2 {
  width: 66.66%;
  height: 100%;
  background-color: #042954;
  border-radius: 10px;
}

.onboarding-progress-fill3 {
  width: 99.99%;
  height: 100%;
  background-color: #042954;
  border-radius: 10px;
}